/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    
    a.button,
    button[type="submit"]:not(.mkdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize();
        @include mkdfButtonOutlineColor();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;
        @include mkdfTransition(all .4s cubic-bezier(0.42, 0, 0.58, 1));

        &:before {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            width: 0;
            content: '';
            color: #000 !important;
            background: #fff;
            z-index: -1;
            @include mkdfTransition(all .4s cubic-bezier(0.42, 0, 0.58, 1));
        }

        &:hover {
            color: #000 !important;
            background-color: transparent;

            &:before {
                left: 0%;
                right: auto;
                width: 100%;
            }
        }
    }

    input[type="submit"] {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize();
        @include mkdfButtonOutlineColor();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include mkdfButtonSolidColor();
        }
    }

    a.added_to_cart {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */