ul.products {
    > .product{
        > .yith-wcqv-button{
            display: none !important;
        }
    }
}

#yith-quick-view-close{
    color: $default-heading-color;
    top: 10px;
    right: 10px;
    opacity: 1;
    border: none;
    font-weight: 400;

    &:hover{
        color: $first-main-color;
    }
}

#yith-quick-view-content{

    ins{
        text-decoration: none;
    }

    div.summary{
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .yith-wcwl-add-to-wishlist{
        width: auto;
        display: inline-block;
    }

    .yith-wcwl-add-button,
    .yith-wcwl-wishlistexistsbrowse,
    .yith-wcwl-wishlistaddedbrowse{
        a{
            font-size: 15px;
            color: $default-heading-color;

            &:after{
                float: left;
                margin-right: 4px;
                color: $default-heading-color;
            }

            &:hover{
                &:after{
                    content: '\f141';
                }
            }
        }
    }

    .yith-wcwl-wishlistaddedbrowse,
    .yith-wcwl-wishlistexistsbrowse {
        a{
            &:after{
                content: '\f141';
            }
        }
        &.show{
            background-color: transparent;
        }
    }
}

%yith-wcqv-button-style {
    .mkdf-yith-wcqv-holder {
        display: inline-block;
        vertical-align: middle;
        height: 27px;

        .yith-wcqv-button {
            position: relative;
            color: #b3b3b3;
            font-size: 27px;
            line-height: 1;
            display: inline-block;

            &:hover {
                color: #fff;
            }

            div {
                background-size: 15px 15px !important;
            }
        }
    }
}

.mkdf-pl-text-inner, .mkdf-pli-text-inner {
    .blockOverlay {
        display: none !important;
    }
}

.mkdf-pl-holder .mkdf-pli-inner .mkdf-pli-text-inner,
.mkdf-plc-holder .mkdf-plc-item .mkdf-plc-text-inner,
ul.products > .product .mkdf-pl-inner .mkdf-pl-text-inner{
    @extend %yith-wcqv-button-style;
}

/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */
#yith-quick-view-modal,
.yith-quick-view.yith-modal {

    .yith-quick-view-overlay {
        background-color: rgba(0,0,0,.8);
    }

    .yith-wcqv-wrapper {
        padding: 0;
        max-width: 1080px;
        height: auto !important;
    }

    .yith-wcqv-main {
        @include mkdfRelativeHolderLayout();
        padding: 0;
        overflow: hidden !important;
        box-shadow: none;
        background-color: $default-background-color !important;
    }

    #yith-quick-view-content {
        .product{
            margin: 0;
        }

        .product-type-grouped {
            .summary {
                p.price {
                    margin: 0 0 30px;
                }
            }
        }

        .product-type-external {
            .summary {
                .mkdf-single-product-share-wish {
                    margin-top: 32px;
                }
            }
        }

        .product-type-variable {

            .summary {
                p.price {
                    margin: 0 0 30px;
                }
            }

            form {
                .select2-container--default{
                    float: none;
                }

                .single_variation_wrap {
                    margin: 5px 0 0;
                }
            }
        }

        .images,
        .summary {
            position: relative;
            float: left;
            width: 50%;
            box-sizing: border-box;
        }

        .images {

            a, img {
                display: block;
            }
        }

        .summary {
            padding: 78px 48px 60px;

            .star-rating > span {
                width: 99% !important;
            }

            .variations {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0 0 20px;
                border: none;
                padding: 0;

                select {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    background-color: #fff;
                    border: 1px solid #e1e1e1;
                    box-sizing: border-box;
                    line-height: 53px;
                    height: 53px;
                    padding: 0 16px 0 16px;
                    margin: 0 -4px 0 0;
                    color: $default-text-color;
                    font-weight: 300;
                    font-size: 13px;
                    font-family: $default-text-font;
                }

                tr {
                    border: 0;
                    display: block;
                    float: left;

                    &:first-child {
                        padding-right: 6px;
                    }
                }

                td {
                    padding: 0;
                    text-align: left;
                    display: block;
                }

                td.label{
                    width: 12%;
                }

                .select2-container{
                    min-width: 180px;
                }

                .reset_variations {
                    color: $default-heading-color;
                    position: relative;
                    top: -5px;
                    right: -10px;
                }
            }

            .single_variation_wrap {
                margin: 35px 0 0;

                .single_variation {
                    margin: 0 0 35px;
                }
            }

            table.group_table {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                border: none;
                padding: 0;

                tbody {
                    display: block;
                }

                tr {
                    border: 1px solid $default-border-color;
                    display: table;
                    margin-bottom: 13px;
                    width: 100%;
                    background-color: #fff;
                }

                td {
                    box-sizing: border-box;
                    padding: 0 10px;
                    text-align: left;
                    display: inline-block;

                    &:first-child {
                        padding: 0;
                    }

                    &.label {
                        text-align: left;
                        vertical-align: middle;
                    }

                    &.price {
                        display: table-cell;
                        font-size: 16px;
                        margin: 0;
                        text-align: center;
                        vertical-align: middle;

                    }
                }

                .mkdf-quantity-buttons {
                    margin: 0;
                    border: 0;
                    background: transparent;
                    padding: 0 10px;

                    .mkdf-quantity-label{
                        display: none;
                    }

                    .mkdf-quantity-input{
                        width: 20px;
                        margin: 0;
                    }
                }

                a {
                    color: $default-text-color;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }

            .mkdf-yith-product-title {
                display: block;
                margin: 0 0 12px;
            }

            div[itemprop="offers"] {
                position: relative;
                display: block;
            }

            .price {
                position: relative;
                display: block;
                margin: 0 0 32px;
                line-height: 1;
                color: $default-heading-color;
                font-size: 30px;
                font-family: $default-text-font;
                font-weight: 600;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 24px;
                    color: $default-text-color;
                }

                ins {
                    text-decoration: none;
                }
            }

            .woocommerce-product-rating {
                @include mkdfRelativeHolderLayout();
                line-height: 1;
                margin: 0 0 27px;
            }

            div[itemprop="description"],
            .woocommerce-product-details__short-description{
                @include mkdfRelativeHolderLayout();
                margin: 0 0 44px;

                p {
                    margin: 0;
                }
            }

            .mkdf-quantity-buttons {
                margin-right: 20px;
            }

            .mkdf-quantity-label {
                display: none;
            }

            form.cart {
                @include mkdfRelativeHolderLayout();
                margin: 0 0 8px;
            }

            .mkdf-single-product-share-wish {
                position: relative;
                display: inline-block;
                width: 100%;
                vertical-align: middle;
                margin: 0 0 34px;

                .yith-wcwl-wishlistexistsbrowse,
                .yith-wcwl-wishlistaddedbrowse {
                    a {
                        &:after {
                            color: $first-main-color;
                        }
                    }
                }

                .clear{
                    display: none;
                }

                > div{
                    padding: 0 20px;

                    &:after{
                        content: "|";
                        color: #d3d3d3;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }

                    &:first-child{
                        padding-left: 0;

                        &:after{
                            display: none;
                        }
                    }

                    &:last-child{
                        padding-right: 0;
                    }
                }

                .mkdf-woo-social-share-holder {
                    float: left;
                    position: relative;

                    > * {
                        display: table-cell;
                        vertical-align: top;
                        line-height: 22px;
                    }

                    > span {
                        font-size: 12px;
                        color: $first-main-color;
                        padding: 0 9px 0 0;
                        font-weight: 700;
                    }
                }
            }

            .product_meta {
                > span {
                    display: block;
                    color: $first-main-color;
                    font-size: 15px;
                    font-weight: 400;
                    margin-bottom: 3px;

                    a {
                        color: $default-text-color;

                        &:hover {
                            color: $first-main-color;
                        }
                    }

                    span {
                        color: $default-text-color;
                    }
                }
            }

            .yith-wcwl-add-to-wishlist {
                width: auto;
                background-color: transparent;
                float: left;

                .yith-wcwl-add-button,
                .yith-wcwl-wishlistaddedbrowse,
                .yith-wcwl-wishlistexistsbrowse {
                    a {
                        font-size: 15px;
                        color: rgba($default-heading-color, .7);
                        letter-spacing: 0;
                        line-height: 29px;
                        font-weight: 300;

                        &:after {
                            font-size: 17px;
                            float: left;
                            padding-right: 10px;
                            line-height: 27px;
                            @include mkdfTransition(color .2s ease-out);
                        }

                        &:hover {
                            color: $first-main-color;

                            &:after {
                                color: $first-main-color;
                            }
                        }
                    }
                }
            }

            p.stock.out-of-stock,
            p.stock.in-stock {
                color: $default-heading-color;
                font-weight: bold;
                margin: 0 0 18px;
            }
        }
    }

    #yith-quick-view-close {
        top: 29px;
        right: 33px;
        width: 30px;
        height: 30px;
        font-size: 0;
        line-height: 30px;
        font-weight: 400;
        text-align: center;
        opacity: 1;
        border: 0;
        color: #fff;

        &:before {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'ElegantIcons';
            content: "\4d";
            font-size: 37px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        &:hover {
            opacity: 0.8;
        }
    }
}
/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */