/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .mkdf-cart-number {
            background-color: #fff;
            color: $default-background-color;
            border-radius: 50%;
            height: 13px;
            width: 13px;
            line-height: 13px;
            text-align: center;
            top: -7px;
            left: -12px;
            position: relative;
            font-weight: 600;
            font-size: 8px;
            font-family: $default-heading-font;
            display: inline-block;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            opacity: 1;
            visibility: visible;
            @include mkdfTransform(translate3d(0, 0, 0));
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 318px;
    background-color: $additional-background-color;
    border: none;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include mkdfTransition(all 0.3s ease-out);
    @include mkdfTransform(translate3d(0, 40px, 0));

    ul {
        margin: 43px 25px 48px 25px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 13px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 76px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding-left: 17px;
        top: -2px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;

            a:hover {
                color: #959595;
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 20px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            &:hover {
                color: #959595;
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 3px 0 0;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: $default-heading-color;
            font-family: $default-heading-font;
            font-weight: 600;
            font-size: 15px;
        }
    }

    .mkdf-cart-bottom {
        padding: 20px 0 0;
        position: relative;
        margin-top: 16px;

        &:before {
            content: '';
            background-color: #fff;
            height: 1px;
            width: 43px;
            position: absolute;
            top: 0;
            left: 0;
        }

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 22px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
                font-family: $default-heading-font;
                font-weight: 600;
                font-size: 15px;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .mkdf-view-cart,
        .mkdf-checkout {
            position: relative;
            display: inline-block;
            height: 50px;
            line-height: 48px;
            padding: 0 26px;
            font-size: 15px;
            font-weight: 600;
            color: #fff;
            background-color: transparent;
            font-family: $default-heading-font;
            border: 1px solid #fff;
            text-align: center;
            box-sizing: border-box;
            z-index: 3;
            @include mkdfTransition(all .4s cubic-bezier(0.42, 0, 0.58, 1));

            span {
                display: block;
            }

            &:before {
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                height: 100%;
                width: 0px;
                content: '';
                color: #000 !important;
                background: #fff;
                z-index: -1;
                @include mkdfTransition(all .4s cubic-bezier(0.42, 0, 0.58, 1));
            }
    
            &:hover {
                color: #000 !important;
                background-color: transparent;
    
                &:before {
                    left: 0%;
                    right: auto;
                    width: 100%;
                }
            }
        }

        .mkdf-view-cart {
            padding: 0 47px;
        }

        .mkdf-checkout {
            float: right;
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */