/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 20px;
    right: 20px;
    width: auto;
    height: 25px;
    margin: 0;
    padding: 0 14px;
    color: $default-background-color;
    font-family: $default-heading-font;
    font-size: 15px;
    font-weight: 600;
    line-height: 23px;
    letter-spacing: -0.005em;
    text-align: center;
    z-index: 90;
    box-sizing: border-box;
    @include mkdfTransform(translateZ(0));
    text-transform: lowercase;
}

@mixin mkdfWooCommerceSaleStyle() {
    background-color: $default-background-color;
    color: #fff;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    background-color: #fff;
}

@mixin mkdfWooCommerceNewProductStyle() {
    background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 9px 0 0;
    line-height: 1;
    color: $default-heading-color;
    font-family: $default-heading-font;
    
    * {
        font-size: 15px;
        line-height: inherit;
        font-weight: 600;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "IonIcons";
    font-size: 20px;
    line-height: 1em;
    letter-spacing: 10px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f4b2\f4b2\f4b2\f4b2\f4b2";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f4b2";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        position: absolute;
        right: 0;
        bottom: 4px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */