/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

	    .mkdf-pli-title {
		    margin: 0 0 4px;

			a:hover {
				color: $default-hover-color;
			}
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }
	
	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
			background-color:  rgba(0,0,0,.8);
            box-sizing: border-box;
            @include mkdfTransition(opacity .2s ease-out);
			z-index: 120;
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

			>.clear {
				display: none;
			}

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
            }
	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
						padding: 0;
						background-color: transparent;

				        &:hover {

				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }

			.mkdf-pl-custom-atc-btn {
				display: inline-block;
				vertical-align: text-top;

				a {
					color: #b3b3b3;
					font-size: 22px;
					line-height: 1;
					padding: 0;


					&:hover {
						color: #fff;
					}
				}

				.added {
					display: none;
				}

				.added_to_cart {
					font-size: 0;
					background-color: transparent;
					padding: 0;
					position: relative;

					&:before {
						content: url("../../assets/img/cart-full.svg");
						position: relative;
						top: -3px;
						left: 1px;
						margin-left: -1px;
					}
				}
			}
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 27px 0;
		text-align: center;
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */